// Styling for the navbar
#mainNav {
  background-color: $gray-900;
  opacity: 0.8;
  .navbar-toggler {
    font-size: 14px;
    right: 0;
    padding: 13px;
    text-transform: uppercase;
    color: white;
    border: 0;
    background-color: none;
    @include heading-font;
  }

  .navbar-brand {
    // color: $logoColor;
    position: relative;
    // color: red;
    @include script-font;
    z-index: 100;
    // z-index: 1000;
    // font-weight: bold;
    &.active,
    &:active,
    &:focus,
    &:hover {
      // color: darken($logoColor, 20%);
      font-weight: bold;
    }
  }
  .navbar-nav {
    .nav-item {
      .nav-link {
        font-size: 90%;
        font-weight: 400;
        padding: 0.75em 0;
        letter-spacing: 1px;
        color: white;
        @include heading-font;
        &.active,
        &:hover {
          font-weight: bold;
          // color: $primary;
        }
      }
    }
  }
}
@media (min-width: 992px) {
  #mainNav {
    padding-top: 25px;
    padding-bottom: 25px;
    -webkit-transition: padding-top 0.3s, padding-bottom 0.3s;
    -moz-transition: padding-top 0.3s, padding-bottom 0.3s;
    transition: padding-top 0.3s, padding-bottom 0.3s;
    border: none;
    background-color: transparent;
    .navbar-brand {
      outline: 0;
      font-size: 1.75em;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      transition: all 0.3s;
    }
    .navbar-nav {
      .nav-item {
        .nav-link {
          padding: 1.1em 1em !important;
        }
      }
    }
    &.navbar-shrink {
      padding-top: 0;
      padding-bottom: 0;
      background-color: $gray-900;
      .navbar-brand {
        font-size: 1.25em;
        padding: 12px 0;
      }
    }
  }
}

.nav-select {
  display: block;
  margin: 0 auto;
  // padding: 0;
  text-align-last: center;
}
